import axiosIns from "@/utils/request";
//获取专辑内容
export const getAlbumApi = (id: number | string) => {
	return axiosIns.get("/album", { params: { id } });
};
//收藏、取消收藏专辑
export const subAlbumApi = ({ id, t }: { id: number | string; t?: number }) => {
	return axiosIns.get("/album/sub", { params: { id, t: t === undefined ? 1 : t, timeStamp: Date.now() } });
};
export const getSubedAlbumListApi = ({ limit, offset }: { limit: number; offset: number } = { limit: 999, offset: 0 }) => {
	return axiosIns.get("/album/sublist", { params: { limit, offset, timeStamp: Date.now() } });
};
